.nav {
  list-style-type: none;
  display: none;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  height: calc(100vh - 63px);
  overflow-y: auto;
}
.nav.open {
  background-color: #fbf9f8;
  position: absolute;
  top: 63px;
  display: flex;
  flex-direction: column;
  right: 0;
  width: 100%;
}
